import Alarm from "../assets/images/logos/Alarm.png";
import NextGreen from "../assets/images/logos/NextGreen.png";
import SmartFarm from "../assets/images/logos/smartFarm.png";
import { Link } from "react-router-dom";
import "../assets/scss/layout/logo.scss"

const Logo = () => {
    return (
        <div className={"logo-div"}>
            <Link to="/">
                <div className={"logo-child"}>
                <img src={Alarm} alt="Alarm Logo" className={"alarm"}/>
                </div>
            </Link>
            <Link to="/">
                <div className={"logo-child"}>
                <img src={NextGreen} alt="NextGreen Logo" className={"nextgreen"}/>
                </div>
            </Link>
            <Link to="/">
                <div className={"logo-child"}>
                <img src={SmartFarm} alt="SmartFarm Logo" className={"smartfarm"} />
                </div>
            </Link>
        </div>
    );
};
export default Logo;
